import axios from 'axios'
import { getToken, removeTokens } from '../helpers/tokens'
const { VUE_APP_API_PATH } = process.env
import store from '@/store/'
import { INVALID_TOKEN_CODE } from '../helpers/constants'
let errorRequests = []
const instance = axios.create({})

const refreshToken = async () => {
  try {
    await store.dispatch('user/refreshTokens')
  } catch(error) {
    console.log(error)
    await store.dispatch('user/logout')
  }
}

const setHeaders = () => {
  return {
    accept: 'application/json',
    'Content-Type': 'application/json'
  }
}

const setUrl = (config) => {
  return `${VUE_APP_API_PATH}${config.url}/`
}

instance.interceptors.request.use(config => {
  
  config.headers = setHeaders()

  if (!config?.data?.refresh) {
    config.url = setUrl(config)
  }
  const token = getToken()
  if (token) {
    config.headers['Authorization'] = `Bearer ${token}`
  }
  return config
}, error => {
  console.log('  , ', error)
  return Promise.reject(error)
})

instance.interceptors.response.use(response => {
  return response
}, async error => {
  errorRequests.push(error)
  if (error?.response?.status === 401) {
    if (error.response.data.code !== INVALID_TOKEN_CODE) return
    if (errorRequests.length >= 5) {
      removeTokens()
      errorRequests = []
    }
    await refreshToken()
    error.config.data = { ...error.config.data, refresh: true }
    return await instance(error.config)
  }
  throw error
})

export const request = async config => {
  try {
    return instance(config)
  } catch(error) {
    throw error
  }
}

export default {
  install: Vue => {
    Vue.prototype.$request = request
  }
}
