<template>
  <a-layout-sider
    collapsible
    class="sider-primary"
    breakpoint="lg"
    collapsed-width="0"
    width="250px"
    :collapsed="sidebarCollapsed"
    @collapse="$emit('toggleSidebar', !sidebarCollapsed)"
    :trigger="null"
    :class="[
      'ant-layout-sider-' + sidebarColor,
      'ant-layout-sider-' + sidebarTheme,
    ]"
    theme="light"
    :style="{ backgroundColor: 'transparent' }"
  >
    <div class="brand">
      <img src="/logo.svg">
      <hr />
    </div>

    <a-menu theme="light" mode="inline">
      <a-menu-item class="menu-item-header">  </a-menu-item>
      <a-menu-item v-for="item in menu" :key="item.path">
        <router-link :to="item.path">
          <span class="icon">
            <v-icon :name="item.icon"></v-icon>
          </span>
          <span class="label">{{ item.name }}</span>
        </router-link>
      </a-menu-item>
      <a-menu-item class="menu-item-header">  </a-menu-item>
      <!-- <a-menu-item>
        <router-link to="/profile">
          <span class="icon">
            <v-icon name="cog"></v-icon>
          </span>
          <span class="label"></span>
        </router-link>
      </a-menu-item> -->
      <a-menu-item @click="$store.dispatch('user/logout')">
        <router-link to="#">
          <span class="icon">
            <v-icon name="sign-out-alt" />
          </span>
          <span class="label"></span>
        </router-link>
      </a-menu-item>
    </a-menu>
  </a-layout-sider>
</template>

<script>
import sidebarMenu from "@/helpers/sidebar-menu";
export default {
  props: {
    // Sidebar collapsed status.
    sidebarCollapsed: {
      type: Boolean,
      default: false,
    },

    // Main sidebar color.
    sidebarColor: {
      type: String,
      default: "primary",
    },

    // Main sidebar theme : light, white, dark.
    sidebarTheme: {
      type: String,
      default: "light",
    },
  },
  data() {
    return {
      menu: sidebarMenu,
    };
  },
};
</script>
