<template>
  <FormulateForm
  @submit="event => $emit('submit', event)"
  @input="handleChange"
  :value="value"
  :values="values"
  :schema="schema"
  :name="name">
    <slot></slot>
</FormulateForm>
</template>

<script>
export default {
  name: 'x-form',
  props: ['value', 'schema', 'submit', 'values', 'name'],
  methods: {
    handleChange(data) {
      this.$emit('input', data)
    }
  }
}
</script>

