<template>
  <FormulateInput
  :label="label"
  label-class="x-input-label"
  :type="type"
  :name="name"
  :placeholder="placeholder"
  :options="options"
  :multiple="multiple"
  :min="min"
  :max="max"
  :maxlength="maxlength"
  :error-behavior="errorBehavior"
  :help="help"
  :validation="validation"
  :accept="accept"
  :disabled="disabled"
  :selectable="selectable"
  :masked="masked"
  :validation-rules="validationRules"
  :validation-messages="validationMessages"
  @input="watchClass"
  step="0.5"
  @focus="event => $emit('focus', event)"
  :class="{'not-empty': value.length, 'required': isRequired, className}">
  </FormulateInput>
</template>

<script>
export default {
  name: 'x-input',
  props: {
    type: {
      required: true,
      type: String
    },
    name: {
      required: true,
      type: String
    },
    /*
    * @prop validation -   [
      [''],
      ['', '']
    ]  '###|###|### etc'
    */
    validation: {
      type: String | Array,
      required: false,
    },
    label: {
      type: String,
      required: false,
    },
    help: {
      type: String,
      required: false,
    },
    /*
    * @prop options -   checkbox, , 
    */
    options: {
      type: Object | Array,
      required: false,
    },
    /*
    * @prop multiple -  type === select
    */
    multiple: {
      type: Boolean,
      required: false,
    },
    placeholder: {
      type: String,
      required: false,
    },
    min: {
      type: String,
      required: false,
    },
    max: {
      type: String,
      required: false,
    },
    maxlength: {
      type: Number,
      required: false,
    },
    errorBehavior: {
      type: String,
      required: false
    },
    masked: {
      type: String,
      required: false
    },
    /*
    * @prop accept -  file
    */
    accept: {
      type: String,
      required: false,
    },
    className: {
      type: String,
      required: false
    },
    selectable: {
      type: Function,
      required: false,
    },
    disabled: {
      type: Boolean,
      required: false
    },
    /*
    * @prop validation-rules -   
    *     
    */
    validationRules: {
      type: Object,
      required: false
    },
    /*
    * @prop validation-messages -   
    *     , 
    */
    validationMessages: {
      type: Object,
      required: false
    }
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    watchClass(event) {
      this.value = event
    }
  },
  computed: {
    isRequired() {
      console.log(this.validation)
      return this.validation ? this.validation.slice('required', 1) : false
    }
  }
}
</script>

