export default {
  install(Vue) {
    Vue.prototype.$errors = {
      show: (errorArray) => {
        for (let item in errorArray) {
          errorArray[item].map(message => {
            Vue.prototype.$toast.error(`${item}: ${message}`)
          })
        }
      },
      showOnce: message => {
        Vue.prototype.$toast.error(message)
      }
    }
  }
}
