export default {
  install(Vue) {
    const Toast = Vue.prototype.$swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 4000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Vue.prototype.$swal.stopTimer)
        toast.addEventListener('mouseleave', Vue.prototype.$swal.resumeTimer)
      }
    })

    Vue.prototype.$toast = {
        error: (title) => Toast.fire({
          icon: 'error',
          title
        }),
        success: (title) => Toast.fire({
          icon: 'success',
          title
        }),
        info: (title) => Toast.fire({
          icon: 'info',
          title
        })
      }
    }
}
