import { getToken, setTokens, removeTokens, getRefreshToken } from '@/helpers/tokens'
import { request } from '@/plugins/request'
import Vue from 'vue'
import router from '../router'
export default {
  state: {
    userInfo: null,
  },
  getters: {

  },
  mutations: {
    SET_USER_INFO: (state, userInfo) => state.userInfo = userInfo,
  },
  actions: {
    async createTokens({ commit, dispatch }, form) {
      try {
        const response = await request({
          method: 'post',
          url: '/token',
          data: form
        })
        response.data.access = `${response.data.access}`
        setTokens(response.data)
      } catch (error) {
        throw error
      }
    },
    async login({ commit, dispatch }, { form, redirect }) {
      try {
        await dispatch('createTokens', form)
        await dispatch('getUserData', redirect)
      } catch (error) {
        throw error
      }
    },
    async verify({ commit, dispatch }, { verify_code, id, redirect }) {
      try {
        const response = await request({
          method: 'post',
          url: '/verify-email',
          data: {
            verify_code,
            id,
          },
        })
        setTokens(response.data)
        await dispatch('getUserData', redirect === 'next' ? '/achievements' : '')
      } catch(err) {
        throw err
      }
    },
    async refreshTokens({ dispatch, commit }) {
      const refresh = getRefreshToken()
      if (!refresh) return
      try {
        const response = await request({
          url: '/api/v1/token/refresh/',
          method: 'post',
          data: { refresh }
        })
        setTokens(response.data)
      } catch (err) {
        await dispatch('logout')
        throw err
      }
    },
    async getUserData({ commit }, redirect) {
      console.log(redirect)
      try {
        const response = await request({
          method: 'get',
          url: '/me',
        })
        commit('SET_USER_INFO', response?.data)
        this._vm.$user.set({ role: response?.data?.role ? response.data.role : 'guest' })
        if (redirect) router.push(redirect)
      } catch(error) {
        throw error
      }
    },
    async logout ({ commit, dispatch }) {
      commit('SET_USER_INFO', null)
      removeTokens()
      this._vm.$user.set({ role: 'guest' })
    },
  },
  namespaced: true
}
