import { request } from '@/plugins/request'
import store from '.'
import router from '../router'

export default {
  state: {
    indicator: null,
    nomination: null,
    period: null,
    'indicator-report-card': null,
    file: null,
    files: [],
    activeProfilePage: null
  },
  getters: {
    optionNomination: state => {
      if (!state.nomination) return []
      return state.nomination.map(item => {
        return { label: item.name, value: item.id }
      })
    },
    optionPeriod: state => {
      if (!state.period) return []
      return state.period.map(item => {
        return { label: item.name, value: item.id }
      })
    },
    metaTabName: state => {
      console.log(Vue.prototype.$route.meta.tabName)
      return Vue.prototype.$route.meta.tabName
    }
  },
  mutations: {
    SET_ENTITY: (state, {entityName, data}) => {
      state[entityName] = data
    },
    SET_FIELD: (state, { key, property, value, type}) => {
      if (!type && !property) { state[key] = value; return }
      if (!type && property) { vue.set(state[key], property, value); return }
      state[key] = {...state[key], ...value}
    },
    DELETE_ITEM: (state, { entityName, id }) => {
      state[entityName].splice(state[entityName].findIndex(e => e.id == id), 1)
    },
    SET_FILES: (state, files) => {
      state.files.splice(0, state.files.length)
      files.forEach(file => {
        state.files.push(file)
      })
    },
    SET_FILE: (state, file) => {
      state.file = file
    },
    REMOVE_FILES: state => {
      state.files.splice(0, state.files.length)
    },
    CLEAR_ACHIEVEMENT_FORM: state => {
      for (let i in state.achievementForm) {
        state.achievementForm[i] = null
      }
      state.document = null
    }
  },
  actions: {
    async getEntities ({ commit }, { entityName, params }) {
      const response = await request({
        url: `/${entityName}`,
        method: 'get',
        params
      })
      commit('SET_ENTITY', { entityName, data: response.data})
      return response
    },
    async deleteItem ({ commit }, { entityName, id }) {
      try {
        await request({
          url: `/${entityName}/${id}`,
          method: 'delete'
        })
        commit('DELETE_ITEM', {
          entityName,
          id
        })
      } catch (error) {
        throw error
      }
    },
    async getItem ({ commit }, { entityName, id, mutationName }) {
      try {
        return request({
          url: `/${entityName}/${id}`,
          method: 'get'
        })
      } catch (error) {
        throw error
      }
    },
    async saveEntity ({dispatch}, { entityName, data, id, method }) {
      try {
        let url = `/${entityName}${method === 'patch' ? `/${id}` : ''}`
        const response = await request({
          url,
          method,
          data
        })
        return response.data
      } catch (error) {
        throw error
      }
    },
  },
  namespaced: true
}
