import Vue from 'vue'
import Antd from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css'
import App from './App'
import UserLayout from './layouts/User'
import DashboardLayout from './layouts/Dashboard'
import router from './router'
import store from './store'
import VueFormulate from '@braid/vue-formulate'
import { ru } from '@braid/vue-formulate-i18n'
import ui from './components/Ui'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'
import VSelect from '@cone2875/vue-formulate-select'
import 'vue-select/dist/vue-select.css'
import DatePicker from '@sum.cumo/vue-datepicker'
import '@sum.cumo/vue-datepicker/dist/Datepicker.css'
import request from '@/plugins/request'
import errorProvider from '@/plugins/errorProvider'
import VueRouterUserRoles from "vue-router-user-roles"
import VueSweetalert2 from 'vue-sweetalert2'
import errorHandler from '@/plugins/errorHandler'
import toasts from '@/plugins/toasts'

import VueAwesomeSwiper from 'vue-awesome-swiper'


Vue.use(VueAwesomeSwiper)


Vue.use(VueFormulate, {
  plugins: [
    ru,
    VSelect,
    DatePicker
  ],
  validationNameStrategy: ['validationName', 'label', 'name', 'type'],
  errorHandler: errorHandler,
  locales: {
    ru: {
      required({ name }) {
        return `  "${name}"`
      }
    }
  },
  locale: 'ru',
  classes: {
    outer: (context, classes) => {
      return classes.concat([
        'x-wrapper',
        `x-wrapper--${context.type}`
      ])
    },
  }
})
// import './plugins/click-away'
import 'sweetalert2/dist/sweetalert2.min.css'
import './scss/app.scss'

const theme = {
  confirmButtonColor: '#6F346B',
  // cancelButtonColor: '#ff7674',
}

Vue.use(VueSweetalert2, theme)
Vue.use(Antd)
Vue.use(VueFormulate)
Vue.use(request)
Vue.use(errorProvider)
Vue.use(toasts)

Vue.use(VueRouterUserRoles, { router })

Vue.component("layout-user", UserLayout)
Vue.component("layout-dashboard", DashboardLayout)
Vue.component('v-icon', Icon)
ui.forEach(component => {
  Vue.component(component.name, component)
})

Vue.directive('focus', {
  inserted: el => {
    el.focus()
  }
})

Vue.config.productionTip = false

const bootstrap = async () => {
  await store.dispatch('user/getUserData')
  // Vue.prototype.$user.set({  role: 'guest'  })
  new Vue({
    render: h => h(App),
    router,
    store,
  }).$mount("#app")
}

bootstrap()
