<template>
  <a-row class="header-overlay">
    <a-col
      :md="{ span: 22, offset: 1 }"
      :xl="{ span: 22, offset: 1 }"
      :lg="{ span: 22, offset: 1 }"
      :xs="{ span: 20, offset: 2 }"
      class="header-container"
    >
      <router-link to="/" class="header-container--logo">
        <img src="/logo.svg" alt="" />
      </router-link>
      <div class="header-menu">
        <menu-list
          :userInfo="userInfo"
          :items="items"
          :isAuth="isAuth"
        ></menu-list>
      </div>
      <button @click="onToggle" class="burger-button">
        <v-icon name="bars"></v-icon>
      </button>
    </a-col>
    <div class="mobile-menu" v-if="toggleMenu">
      <a-row>
        <a-col :span="22" :offset="1">
          <menu-list
            @set-toggle="onToggle"
            :userInfo="userInfo"
            :items="items"
            :isAuth="isAuth"
          ></menu-list>
        </a-col>
      </a-row>
    </div>
  </a-row>
</template>

<script>
import MenuList from '@/components/Student/Menu'

export default {
  components: {
    MenuList,
  },
  data() {
    return {
      toggleMenu: false,
      items: [
        { name: ' ', path: '/about' },
        { name: '', path: '/gallery' },
        { name: ' ', path: '/history' },
        { name: ' " "', path: '/club' },
        { name: ' ', path: '/organizers' },
      ],
      collapseNav: 0,
    }
  },
  methods: {
    onToggle() {
      this.toggleMenu = !this.toggleMenu
    },
  },
  computed: {
    userInfo() {
      return this.$store.state.user.userInfo
    },
    isAuth() {
      return this.$user.get().role === 'user'
    },
  },
}
</script>

<style lang="scss">
.header-overlay {
  box-shadow: 0px 4px 23px rgba(0, 0, 0, 0.07);
  z-index: 2;
  background: #fff;
  display: flex;
  flex-direction: column;
}
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;

  .header-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .header-menu-list {
      list-style: none;
      display: flex;
      align-items: center;
      gap: 2rem;
      margin: 0;
    }
  }

  .mobile-menu,
  .burger-button {
    display: none;
  }

  .mobile-menu {
    height: 100%;
  }
}

.header-buttons {
  display: flex;
}

@media screen and (max-width: 1200px) {
  .header-menu {
    display: none !important;
  }

  .mobile-menu {
    display: flex;
    flex-direction: column;
    .header-menu-list {
      padding: 40px 15px;
      display: flex;
      flex-direction: column;
      list-style: none;
      display: flex;
      gap: 1rem;
      margin: 0;

      &--item {
        border-bottom: 1px solid #f8f8f8;
        padding-bottom: 1rem;
        a {
          color: #1d1d1b;

          &:hover {
            text-decoration: underline;
          }
        }
      }
      &--item.li-button {
        border: none;
      }

      .inline-buttons {
        display: flex;
        flex-direction: row;
      }

      .right-item {
        margin: 0;
      }
    }
  }

  .burger-button {
    display: block !important;
    background: transparent;
    border: none;
    min-width: 20px;
    min-height: 20px;
    svg {
      min-width: 20px;
      min-height: 20px;
      transform: translateY(2px);
    }
  }

  @media screen and (max-width: 500px) {
    .header-menu-list {
      &--item {
      }

      .inline-buttons {
        li {
          display: flex;
          width: 100%;
          a {
            width: 100%;
          }
        }
      }
    }
  }
}
</style>
