<template>
  <a-layout class="layout-user" id="layout-user" :class="[layoutClass]">
    <user-header></user-header>
    <a-layout-content>
      <div :class="[additionalClass]">
        <router-view />
      </div>
    </a-layout-content>
    <footer-component v-if="layoutClass && layoutClass.includes('guest')"></footer-component>
  </a-layout>
</template>

<script>
import UserHeader from "../components/Headers/UserHeader"
import FooterComponent from '@/components/Student/Footer'
export default {
  components: {
    FooterComponent,
    UserHeader,
  },
  data() {
    return {}
  },
  computed: {
    // Sets layout's element's class based on route's meta data.
    layoutClass() {
      return this.$route.meta.layoutClass
    },
    additionalClass() {
      return this.$route.meta.additionalClass
    }
  },
};
</script>
